.profile__avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-right: 30px;
    object-fit: cover;
}


.profile__avatar:hover {
    opacity: .8;
}

@media screen and (max-width:1020px) {
    .profile__avatar {
        margin-bottom: 0;
        margin-right: 0;
    }
}